<template>
  <div class = "modal modal-disclaimer" style="display: flex">
    <InfoHead
      :head = actualState.data.head
    />
    <div class="modal_footer">
        <button
        v-for="(item,index) in actualState.data.btnData"
        :key="index"
        @click="choiceButton(item.intoStep)"
        >
        {{item.name}}
        </button>

    </div>
  </div>

</template>

<script>
import InfoHead from '@/components/InfoHead.vue'
export default {
    components: {
        InfoHead
    },
    props: {
        actualState: {}
    },
    methods: {
        choiceButton(intoStep) {
            this.$emit("choice",intoStep)
        }
    }

}
</script>

