<template>
  <div class="questions box" style="display: flex">
    <div class="header">
      <img src="../assets/images/doctor2.svg">
      <div class="title">Выберите вариант:

      </div>
    </div>
        
    <div 
      v-if="actualState.type === 'answer'"
      class="body"      
      >

      <div
        v-for="(item, index) in actualState.data.answers"
        :key="index"
        @click = actionAnswer(item.action)
        class="question"
      >
      {{item.text}}

      </div>

    </div>

    <div 
      v-if="actualState.type === 'groupanswer'"
      class="body"      
      >

      <div
        v-for="(item, index) in answers"
        :key="index"
        @click = actionGroupAnswer(index,item.action)
        class="question"
      >
      {{item.text}}

      </div>

    </div>
  </div>
</template>

<script>
export default {
  props: {
    actualState: {},
    answers: []
  },
  methods: {
    actionGroupAnswer(index, action) {
      this.$emit("actionanswer", action)
      if (action.data.action.type !== "next") {
        this.$emit("deleteanswer", index)
      } 
    },
    actionAnswer(action) {
      this.$emit("actionanswer", action)
    }
  }
}
</script>

