<template>
  <div class="content">
    <div class="chart box" ref="chartbox">
      
      <ChatMessage
        v-for="(item, index) in chatList" :key="index"
        :message = chatList[index]
      />
      
      <!-- <button
        @click="actionAnswer({'type': 'next'})"
      >Далее</button> -->
      
      <ImageModal
        v-if="actualState.data.showImage>0"
        :numberImage = actualState.data.showImage
      />

    </div>
    <ChatQuestions

      :actualState = actualState
      :answers = answers
      @actionanswer = actionAnswer($event)
      @deleteanswer = deleteanswer($event)
      />
  </div>

</template>

<script>
import ChatMessage from "@/components/ChatMessage.vue"
import ChatQuestions from "@/components/ChatQuestions.vue"
import ImageModal from '@/components/ImageModal.vue'

export default {
  components: {
    ChatMessage,
    ChatQuestions,
    ImageModal
  },
  props: {
    chatList: [],
    actualState: {},
    answers:[]
  },
  methods: {
    actionAnswer(action) {
      this.$emit("actionanswer", action)
    },
    deleteanswer(index) {
      this.$emit("deleteanswer", index)
    }
  },
  updated() {
    this.$refs.chartbox.scrollTop = this.$refs.chartbox.scrollHeight
  }
}
</script>

<style>

/* CHART */
.chart {
  flex-basis: 60%;
  color: #932600;
  overflow-y: overlay;
  padding-bottom: 30px;
  margin-right: 4vw;
}
.chart > div > span {
  position: absolute;
  font-weight: 300;
  right: 10px;
  bottom: 3px;
  font-size: 0.7em;
  opacity: 0.8;
}

.chart > div {
  max-width: 24vw;
}
.chart > .medic {
  position: relative;
  align-self: flex-end;
  background-color: #c0d9fe;
  padding: 10px;
  padding-left: 20px;
  margin-top: 10px;
  margin-right: 40px;
  border: 1px solid #00395e;
  border-radius: 10px;
  color: #00395e;
  width: 70%;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.chart > .medic::before {
  content: "";
  position: absolute;
  bottom: -30px;
  right: -30px;
  width: 23px;
  height: 30px;
  background-image: url("../assets/images/doctor2.svg");
  background-size: cover;
}

.chart > .medic::after {
  content: "";
  position: absolute;
  bottom: -4.9px;
  right: 3px;
  border-bottom: 10px solid #c0d9fe;
  border-left: 70px solid transparent;
  border-right: 10px solid transparent;
  height: 0;
  width: 80px;
  transform: rotate(4deg);
  -webkit-transform: rotate(4deg);
  -moz-transform: rotate(4deg);
  -ms-transform: rotate(4deg);
  -o-transform: rotate(4deg);
}
.chart > .medic > .dialog-arrow {
  position: absolute;
  bottom: -6.5px;
  right: 1px;
  border-bottom: 10px solid #00395e;
  border-left: 70px solid transparent;
  border-right: 10px solid transparent;
  height: 0;
  width: 70px;
  transform: rotate(4deg);
  -webkit-transform: rotate(4deg);
  -moz-transform: rotate(4deg);
  -ms-transform: rotate(4deg);
  -o-transform: rotate(4deg);
  z-index: 0;
}

.chart > .patient {
  position: relative;
  align-self: flex-start;
  background-color: #f7e4ee;
  padding: 10px;
  padding-left: 20px;
  margin-top: 10px;
  margin-left: 40px;
  border: 1px solid #c9355d;
  border-radius: 10px;
  width: 70%;
  color: #c9355d;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.chart > .patient::before {
  content: "";
  position: absolute;
  bottom: -30px;
  left: -30px;
  width: 30px;
  height: 30px;
  background-image: url("../assets/images/patient2.svg");
  background-position-x: 0px;
  background-repeat: no-repeat;
  background-size: contain;
}
.chart > .patient::after {
  content: "";
  position: absolute;
  bottom: -4.9px;
  left: 3px;
  border-bottom: 10px solid #f7e4ee;
  border-right: 70px solid transparent;
  border-left: 10px solid transparent;
  height: 0;
  width: 80px;
  transform: rotate(-4deg);
  -webkit-transform: rotate(-4deg);
  -moz-transform: rotate(-4deg);
  -ms-transform: rotate(-4deg);
  -o-transform: rotate(-4deg);
}
.chart > .patient > .dialog-arrow {
  position: absolute;
  bottom: -6.5px;
  left: 1px;
  border-bottom: 10px solid #c9355d;
  border-right: 70px solid transparent;
  border-left: 10px solid transparent;
  height: 0;
  width: 70px;
  transform: rotate(-4deg);
  -webkit-transform: rotate(-4deg);
  -moz-transform: rotate(-4deg);
  -ms-transform: rotate(-4deg);
  -o-transform: rotate(-4deg);
  z-index: 0;
}

.chart .load {
  position: relative;
  font-size: 2em;
  line-height: 0.7em;
  width: 30px;
  white-space: nowrap;
  overflow: hidden;
  -webkit-animation: type 0.5s steps(50, end) infinite;
  animation: type 0.5s steps(50, end) infinite;
}

/* QUESTIONS BOX */
.questions {
  flex-basis: 30%;
  display: none;
  flex-direction: column;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.8);
  border: 2px solid #00395e;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.questions > .header {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 20px;
}
.questions > .header > img {
  width: 12%;
  margin-right: 2vw;
}
.questions > .header > .title {
  font-size: 2.2vw;
  color: #00395e;
}
.questions > .body {
  display: flex;
  flex-direction: column;
  height: auto;
  max-height: 100%;
  overflow-y: overlay;
}
.questions > .body > .question {
  /* width: 100%; */
  width: 90%;
  margin: 7px 0px;
  padding: 10px;
  background-color: rgba(230, 230, 230, 0.6);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  cursor: pointer;
  color: #000;
}
.questions > .body > .question:hover,
.questions > .body > .question:active {
  background-color: #f7e4ee;
  color: #00395e;
}

/* END QUESTIONS BOX */
</style>