<template>
  <div class="accept">
    <input id="accept" type="checkbox" name="ok" v-model="checked" @change="changeCheckbox">
    <label for="accept">{{checkboxText}}</label>  
  </div>
</template>

<script>
export default {
    data() {
      return {
        checked: false
      }
    },
    props: {
      checkboxText: String
    },
    methods: {
      changeCheckbox() {
        this.$emit("changecheckbox", this.checked)
      }
    },
    mounted() {
      this.changeCheckbox()
    }

}
</script>

