<template>
  <div class = "modal modal-disclaimer" style="display: flex">
    <InfoHead
      :head = modalInfo.head
    />
    <InfoBody
      v-if="modalInfo.body!==''"
      :body = modalInfo.body
    />
    <InfoFooter
      :footerData = modalInfo.footer
      @action = buttonAction($event)
    />
  </div>
</template>

<script>
import InfoHead from '@/components/InfoHead.vue'
import InfoBody from '@/components/InfoBody.vue'
import InfoFooter from '@/components/InfoFooter.vue'

export default {
    components: {
        InfoHead,
        InfoBody,
        InfoFooter
    },
    props: {
      modalInfo: {}
    },
    methods: {
      buttonAction(action) {
        this.$emit("action", action)
      }
    },
    mounted() {
      // console.log(this.actualStateData)
  }

}
</script>

<style>
/* MODAL WINDOW */
.modal {
