<template>
  <div class="container">

       <ViewChat
        v-if="showChat"
        :chatList = chatList
        :actualState = actualState
        :answers = answers
        @pushmessage = pushToChatList()
        @action = buttonAction($event)
        @actionanswer = buttonAction($event)
        @deleteanswer = deleteanswer($event)
      />

      <InfoModal
        v-if="showModal"
        :modalInfo = modalInfo
        @action = buttonAction($event)
      />

      <ChoiceStep
        v-if="actualState.type==='choice'"
        :actualState = actualState
        @choice = choiceStep($event)
      />

    </div>
</template>

<script>
import InfoModal from '@/components/InfoModal.vue'
import ViewChat from '@/components/ViewChat.vue'
import ChoiceStep from '@/components/ChoiceStep.vue'
import MainData from '@/data/data.json'

export default {
  name: 'App',
  data() {
    return {
      stepNumber: 0,
      showModal: false,
      showImageModal: false,
      modalInfo: {},
      modalImageData: {},
      showChat: true,
      answers: [],
      chatList: [],
      mainBranch: MainData,
      actualState: {}
    }
  },
  components: {
    InfoModal,
    ViewChat,
    ChoiceStep
  },
  methods: {
    addMessages(talk) {      
      for (let i = 0; i < talk.length; i++) {
        // setTimeout(() => {this.chatList.push(talk[i])
        // }, 2000)
        this.chatList.push(talk[i])
      }
    },

    choiceStep(step) {
      this.stepNumber = step
      this.actualState = this.mainBranch[step]
      this.buttonAction(this.actualState)
    },

    buttonAction(action) {
      if ((action.type === "next")&&(this.stepNumber < this.mainBranch.length-1)) {
        this.stepNumber += 1
        this.actualState = this.mainBranch[this.stepNumber]
        this.showModal = false
        if (this.actualState.type === "info") {
          this.buttonAction(this.actualState)
        }
        if (this.actualState.type === "talk") {
          this.buttonAction(this.actualState)
        }
        if (this.actualState.type === "groupanswer") {
          this.buttonAction(this.actualState)
        }
      }
      else if ((action.type === "close")){
        this.showModal = false
      }
      else if ((action.type === "info")){
        this.modalInfo = action.data
        this.showModal = true
        // setTimeout(() => {
        //   this.showModal = true
        // }, 1000)
      }
      else if ((action.type === "talk")){
        this.addMessages(action.data.talk)
        if (action.data.action.type === "next"){
          setTimeout(() => {this.buttonAction(action.data.action)
        }, 1000)
          // this.buttonAction(action.data.action)
        }
      }
      else if ((action.type === "answer")){
        this.actualState = action
        this.showModal = false
      }
      else if ((action.type === "groupanswer")){
        this.actualState = action
        this.showModal = false
        for (let i = 0; i < this.actualState.data.answers.length; i++) {
          this.answers.push(this.actualState.data.answers[i])
        }
      }
    },
    deleteanswer(index) {
      this.answers.splice(index,1)
      if (this.answers.length === 0) {
        this.buttonAction({"type": "next"})
      }
    }
  },
  created() {
    this.actualState = this.mainBranch[this.stepNumber]
    if (this.actualState.type === "info") {
      this.modalInfo = this.actualState.data
      this.showModal = true
    }
    if (this.actualState.type === "groupanswer") {
      for (let i = 0; i < this.actualState.data.answers.length; i++) {
          this.answers.push(this.actualState.data.answers[i])
        }
    }
  }
}
</script>

<style>
