<template>
  <div class="close-disclaimer">
    <button
    @click = buttonAction
    :disabled='isDisabled'
    >{{name}}</button>
  </div>
</template>

<script>
export default {
  props: {
    name: String,
    action: {},
    isDisabled: Boolean
  },
  methods: {
    buttonAction() {
      this.$emit("action", this.action)
    }
  }
}
</script>

